.glitch, .glitch-once {
  position: relative;
  color: #000;
  mix-blend-mode: lighten;
  display: inline-block;
  will-change: clip; }

.glitch:after, .glitch:before, .glitch-once:after, .glitch-once:before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  width: 100%;
  clip: rect(0, 0, 0, 0); }

.glitch:before, .glitch-once:before {
  left: -2px;
  text-shadow: 2px 0 #fff; }

.glitch:after, .glitch-once:after {
  left: 4px;
  text-shadow: -2px 0 #fff; }

.glitch.is-hover:before, .glitch:hover:after, .glitch:hover:before {
  animation: f 3.25s infinite cubic-bezier(0.08, 0.62, 0.18, 0.96) alternate-reverse; }

.glitch.is-hover:before, .glitch-once:before {
  text-shadow: 3px 0 #fff; }

.glitch.is-hover:after {
  text-shadow: 3px 0 #fff;
  animation: f 3.25s infinite cubic-bezier(0.08, 0.62, 0.18, 0.96) alternate-reverse; }

.glitch-once:after {
  text-shadow: 3px 0 #fff;
  animation: f 3.25s cubic-bezier(0.08, 0.62, 0.18, 0.96);
  animation-delay: 1.2s; }

.glitch.-button:hover:before {
  text-shadow: 3px 0 #fff; }

.glitch.-button:hover:after, .glitch.-button:hover:before {
  animation: g 1.5s infinite cubic-bezier(0.08, 0.62, 0.18, 0.96) alternate-reverse; }

.glitch.-button:hover:after {
  text-shadow: 3px 0 #fff; }

